.container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  grid-gap: 50px;
  align-items: stretch;
}
.grid {
  align-self: center;
}

article {
  border: 1px solid #ccc;
  box-shadow: 2px 2px 6px 0px rgba(0, 0, 0, 0.3);
  text-align: center;
}

article .imgBox {
  position: relative;
  display: flex;
  justify-content: center;
}

article .imgBox img {
  max-height: 300px;
}

article .imgBox span {
  font-family: "Archivo Black", sans-serif;
  color: white;
  text-transform: uppercase;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
  position: absolute;
}

article .imgBox .topText {
  top: 8px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

article .imgBox .bottomText {
  bottom: 8px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

#main-logo {
  height: 130px;
  max-width: 500px;
}

.titlecolor {
  text-align: center;
  font-size: xx-large;
  color: #fff;
  padding-bottom: 2em;
  padding-top: 2em;
}

.titlecolor h1{
  font-weight: bolder;
}

.box {
  padding: 30px;
}

.icon {
  border: none;
  text-decoration: none;
  margin: 20px;
}
.navbar-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: small;
}
.is-small.delete {
  top: 2px;
  margin-left: 4px;
}

.box {
  width: 100%;
  font-size: 18px;
  height: -webkit-min-content;
  height: min-content;
  text-align: center;
}


